import { Link } from "react-router-dom";

const Company = () => {
  return (
    <div>
      <ul>
        <li>
          <Link to="/Company">회사소개</Link>
        </li>
        <li>
          <Link to="/Product">제품소개</Link>
        </li>
      </ul>
      <h1>회사소개</h1>
      <p>회사소개 페이지입니다.</p>
    </div>
  );
};

export default Company;
